import Duration from "~/components/Duration";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      box: false,
      headers: [
        {
          text: "Date",
          align: "start",
          sortable: false,
          value: "date"
        },
        { text: "Remarks", value: "remarks" }
        // { text: "Block", value: "block" },
        // { text: "Duration (min)", value: "duration" }
      ],
      status2: [
        {
          name: "Soil NPK",

          remarks: ""
        },
        {
          name: "Soil pH",

          remarks: ``
        },
        {
          name: "Soil EC",

          remarks: ""
        },
        {
          name: "Soil Moisture",
          remarks: ""
        },
        {
          name: "Soil Temperature",

          remarks: ""
        },
        {
          name: "Leaf Temperature",

          remarks: ""
        },
        {
          name: "Humidity",

          remarks: ""
        },
        {
          name: "Water NPK",

          remarks: ""
        },
        {
          name: "Water pH",

          remarks: ""
        },
        {
          name: "Water EC",

          remarks: ""
        }
      ]
    };
  },
  computed: {
    ...mapState({
      status: state => state.stations[state.activeUser].status,
      activeUser: state => state.activeUser
      // duration: state => state.duration
    })
  },
  props: ["allDate"],
  mounted() {
    // console.log(this.allDate);
  },
  methods: {
    ...mapMutations({
      updateDateState: "updateDateState"
    }),
    handleClick(row) {
      console.log(row.date);
      this.date = row.date;
      // this.userID = row.userID;
      // this.username = row.username;
      // (this.topics = row.topics), (this.deviceID = row.deviceID);
      this.box = true;
      // console.log(this.userID, this.topics)
    },
    cancel() {
      this.box = false;
      this.editBox = false;
    },
    deleteSchedule() {
      let api;
      if (this.activeUser == 0) {
        api = "http://139.59.109.48/api/schedule/ipah1";
      } else if (this.activeUser == 1) {
        api = "http://139.59.109.48/api/schedule/ipah2";
      } else if (this.activeUser == 2) {
        api = "http://139.59.109.48/api/schedule/tkpmPagoh";
      } else {
        api = "http://139.59.109.48/api/schedule/kongPo";
      }
      this.$axios
        .$delete(api, {
          data: {
            date: this.date
          }
        })
        .then(response => {
          // this.getUserDataWithTopic(response)
          // this.getAllUserWithTopic()
          this.box = false;
          window.location.reload();
        })
        .catch(error => {
          console.log(error);
        });
      // setTimeout(() => {
      //   window.location.reload();
      // }, 3000);
      // console.log("here");
      // this.box = false;
    }
  }
};
